/* Modern Resume Form Styles */

.resume-form-container {
  min-height: 100vh;
  background: linear-gradient(135deg, var(--gray-50) 0%, white 50%, var(--primary-50) 100%);
  position: relative;
}

/* Form Header */
.form-header {
  background: linear-gradient(135deg, var(--primary-600), var(--primary-700));
  padding: var(--space-12) 0 var(--space-16) 0;
  color: white;
  position: relative;
  overflow: hidden;
}

.form-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg width="60" height="60" viewBox="0 0 60 60" xmlns="http://www.w3.org/2000/svg"><g fill="none" fill-rule="evenodd"><g fill="%23ffffff" fill-opacity="0.1"><circle cx="30" cy="30" r="2"/></g></svg>') repeat;
  pointer-events: none;
}

.form-header-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 var(--space-6);
  text-align: center;
  position: relative;
  z-index: 1;
}

.form-title {
  font-family: var(--font-heading);
  font-size: clamp(2rem, 4vw, 3rem);
  font-weight: var(--font-bold);
  margin-bottom: var(--space-4);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--space-3);
}

.form-icon {
  font-size: 1.2em;
  animation: bounce 2s ease-in-out infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
  40% { transform: translateY(-10px); }
  60% { transform: translateY(-5px); }
}

.form-subtitle {
  font-size: var(--text-xl);
  opacity: 0.9;
  margin-bottom: var(--space-6);
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.resume-type-badge {
  display: inline-flex;
  align-items: center;
  gap: var(--space-2);
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  padding: var(--space-3) var(--space-6);
  border-radius: var(--radius-full);
  border: 1px solid rgba(255, 255, 255, 0.3);
  font-weight: var(--font-semibold);
}

.badge-icon {
  font-size: 1.2em;
}

.resume-form h2 {
  color: #2c3e50;
  margin-top: 30px;
  margin-bottom: 15px;
  padding-bottom: 8px;
  border-bottom: 2px solid #eaeaea;
  font-weight: 600;
  font-size: 1.4rem;
}

.section-header-with-options {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid #eaeaea;
  margin-top: 30px;
  margin-bottom: 15px;
}

.section-header-with-options h2 {
  margin: 0;
  border-bottom: none;
  padding-bottom: 8px;
}

.fresher-option {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 0.9rem;
  color: #555;
}

.fresher-option input[type="checkbox"] {
  margin: 0;
  cursor: pointer;
}

.resume-form .form-group {
  margin-bottom: 20px;
  width: 100%;
}

.resume-form .form-row {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.resume-form .form-row .form-group {
  flex: 1;
  min-width: 200px;
}

.resume-form label {
  display: block;
  margin-bottom: 6px;
  font-weight: 500;
  color: #444;
  font-size: 0.9rem;
}

.resume-form input[type="text"],
.resume-form input[type="email"],
.resume-form input[type="tel"],
.resume-form input[type="file"],
.resume-form textarea {
  width: 100%;
  padding: 10px 12px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 1rem;
  transition: border-color 0.2s, box-shadow 0.2s;
}

.resume-form input[type="file"] {
  padding: 8px;
}

.resume-form input:focus,
.resume-form textarea:focus {
  outline: none;
  border-color: #3498db;
  box-shadow: 0 0 0 2px rgba(52, 152, 219, 0.2);
}

.resume-form textarea {
  resize: vertical;
  min-height: 100px;
}

.form-text {
  display: block;
  margin-top: 4px;
  font-size: 0.8rem;
  color: #777;
}

.section-item {
  margin-bottom: 25px;
  padding: 15px;
  border: 1px solid #eaeaea;
  border-radius: 6px;
  background-color: #fafafa;
}

.achievement-item {
  display: flex;
  align-items: center;
  gap: 10px;
}

.achievement-item .form-group {
  flex: 1;
}

.section-actions {
  margin: 10px 0 20px;
  text-align: right;
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.section-item-actions {
  display: flex;
  justify-content: flex-end;
  margin-top: 10px;
}

.btn-add {
  background-color: #2ecc71;
  color: white;
  border: none;
  padding: 8px 15px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: 500;
  transition: background-color 0.2s;
}

.btn-add:hover {
  background-color: #27ae60;
}

.btn-remove {
  background-color: #e74c3c;
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.85rem;
  transition: background-color 0.2s;
}

.btn-remove:hover {
  background-color: #c0392b;
}

.btn-remove-all {
  background-color: #e74c3c;
  color: white;
  border: none;
  padding: 8px 15px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: 500;
  transition: background-color 0.2s;
  margin-left: 10px;
}

.btn-remove-all:hover {
  background-color: #c0392b;
}

.form-actions {
  margin-top: 30px;
  text-align: center;
}

.btn-generate {
  background-color: #3498db;
  color: white;
  border: none;
  padding: 12px 24px;
  font-size: 1.1rem;
  font-weight: 600;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.btn-generate:hover {
  background-color: #2980b9;
}

.btn-generate:disabled {
  background-color: #95a5a6;
  cursor: not-allowed;
}

/* Optional: Dark mode support */
@media (prefers-color-scheme: dark) {
  .resume-form {
    background-color: #2c3e50;
    color: #ecf0f1;
  }
  
  .resume-form h2 {
    color: #ecf0f1;
    border-bottom-color: #34495e;
  }
  
  .resume-form label {
    color: #bdc3c7;
  }
  
  .resume-form input,
  .resume-form textarea {
    background-color: #34495e;
    color: #ecf0f1;
    border-color: #7f8c8d;
  }
  
  .section-item {
    background-color: #34495e;
    border-color: #2c3e50;
  }
  
  .form-text {
    color: #bdc3c7;
  }
}

/* Color picker styles */
.color-picker-container {
  display: flex;
  align-items: center;
  gap: 15px;
}

.color-picker {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 50px;
  height: 50px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
}

.color-picker::-webkit-color-swatch-wrapper {
  padding: 0;
}

.color-picker::-webkit-color-swatch {
  border: 2px solid #ddd;
  border-radius: 5px;
}

.color-value {
  font-size: 0.9rem;
  color: #666;
  font-family: monospace;
}

/* AI Integration Styles */
.input-with-ai {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.ai-generate-btn, .ai-improve-btn {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 6px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 6px;
  align-self: flex-start;
  margin-top: 5px;
}

.ai-generate-btn:hover, .ai-improve-btn:hover {
  background: linear-gradient(135deg, #5a6fd8 0%, #6a4190 100%);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
}

.ai-generate-btn:disabled, .ai-improve-btn:disabled {
  background: #ccc;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.ai-sidebar-toggle {
  position: fixed;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 12px;
  border-radius: 50%;
  cursor: pointer;
  z-index: 1000;
  box-shadow: 0 4px 20px rgba(102, 126, 234, 0.3);
  transition: all 0.3s ease;
}

.ai-sidebar-toggle:hover {
  transform: translateY(-50%) scale(1.1);
  box-shadow: 0 6px 25px rgba(102, 126, 234, 0.4);
}

.ai-sidebar {
  position: fixed;
  top: 0;
  right: 0;
  width: 400px;
  height: 100vh;
  background: white;
  box-shadow: -4px 0 20px rgba(0, 0, 0, 0.1);
  transform: translateX(100%);
  transition: transform 0.3s ease;
  z-index: 999;
  overflow-y: auto;
  padding: 20px;
}

.ai-sidebar.open {
  transform: translateX(0);
}

/* AI Helper Buttons */
.label-with-ai {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.ai-helper-btn {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.85rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  box-shadow: 0 2px 10px rgba(102, 126, 234, 0.3);
}

.ai-helper-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.4);
}

.ai-helper-btn.loading {
  opacity: 0.7;
  cursor: not-allowed;
  animation: pulse 1.5s infinite;
}

.ai-optimize-btn {
  background: linear-gradient(135deg, #11998e 0%, #38ef7d 100%);
  color: white;
  border: none;
  padding: 1rem 2rem;
  border-radius: 25px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-right: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  box-shadow: 0 4px 15px rgba(17, 153, 142, 0.3);
}

.ai-optimize-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(17, 153, 142, 0.4);
}

.ai-optimize-btn.loading {
  opacity: 0.7;
  cursor: not-allowed;
  animation: pulse 1.5s infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 0.7; }
  50% { opacity: 0.9; }
}

/* ===========================================
   RESPONSIVE DESIGN - MOBILE FIRST APPROACH
   =========================================== */

/* Large Mobile / Small Tablet (768px and below) */
@media (max-width: 768px) {
  .resume-form-container {
    padding: 0;
  }

  .form-header {
    padding: var(--space-8) 0 var(--space-10) 0;
  }

  .form-header-content {
    padding: 0 var(--space-4);
  }

  .form-title {
    font-size: clamp(1.8rem, 5vw, 2.5rem);
    flex-direction: column;
    gap: var(--space-2);
  }

  .form-subtitle {
    font-size: 1rem;
    line-height: 1.5;
  }

  .form-content {
    max-width: 100%;
    margin: 0 auto;
    padding: var(--space-6) var(--space-4);
  }

  .resume-form {
    padding: var(--space-6) var(--space-4);
    margin: 0;
    border-radius: 16px;
  }

  .resume-form h2 {
    font-size: 1.5rem;
    margin-bottom: var(--space-4);
    text-align: center;
  }

  .form-section {
    margin-bottom: var(--space-6);
  }

  .form-row {
    flex-direction: column;
    gap: var(--space-4);
  }

  .form-group {
    width: 100%;
  }

  .form-group label {
    font-size: 0.9rem;
    margin-bottom: var(--space-2);
  }

  .form-input,
  .form-textarea,
  .form-select {
    padding: 12px 16px;
    font-size: 16px; /* Prevents zoom on iOS */
    border-radius: 8px;
  }

  .form-textarea {
    min-height: 120px;
  }

  /* Experience and Education sections for mobile */
  .experience-item,
  .education-item {
    padding: var(--space-4);
    margin-bottom: var(--space-4);
    border-radius: 12px;
  }

  .experience-header,
  .education-header {
    flex-direction: column;
    align-items: flex-start;
    gap: var(--space-2);
  }

  .remove-btn {
    align-self: flex-end;
    margin-top: var(--space-2);
  }

  /* Buttons for mobile */
  .btn-add,
  .btn-generate {
    width: 100%;
    padding: 14px 20px;
    font-size: 1rem;
    margin-bottom: var(--space-3);
  }

  .form-actions {
    flex-direction: column;
    gap: var(--space-3);
    align-items: stretch;
  }

  .ai-optimize-btn {
    width: 100%;
    padding: 12px 20px;
    font-size: 0.9rem;
  }
}

/* Small Mobile (480px and below) */
@media (max-width: 480px) {
  .form-header {
    padding: var(--space-6) 0 var(--space-8) 0;
  }

  .form-header-content {
    padding: 0 var(--space-3);
  }

  .form-title {
    font-size: clamp(1.5rem, 6vw, 2rem);
  }

  .form-subtitle {
    font-size: 0.9rem;
  }

  .form-content {
    padding: var(--space-4) var(--space-3);
  }

  .resume-form {
    padding: var(--space-4) var(--space-3);
    border-radius: 12px;
  }

  .resume-form h2 {
    font-size: 1.3rem;
  }

  .form-section {
    margin-bottom: var(--space-5);
  }

  .form-input,
  .form-textarea,
  .form-select {
    padding: 10px 12px;
    font-size: 16px;
  }

  .form-textarea {
    min-height: 100px;
  }

  .experience-item,
  .education-item {
    padding: var(--space-3);
  }

  .btn-add,
  .btn-generate {
    padding: 12px 16px;
    font-size: 0.9rem;
  }
}

/* Very Small Mobile (360px and below) */
@media (max-width: 360px) {
  .form-header-content {
    padding: 0 var(--space-2);
  }

  .form-content {
    padding: var(--space-3) var(--space-2);
  }

  .resume-form {
    padding: var(--space-3) var(--space-2);
  }

  .form-title {
    font-size: 1.4rem;
  }

  .resume-form h2 {
    font-size: 1.2rem;
  }

  .form-input,
  .form-textarea,
  .form-select {
    padding: 8px 10px;
  }
}

/* Tablet Portrait (768px to 1024px) */
@media (min-width: 769px) and (max-width: 1024px) {
  .form-content {
    max-width: 90%;
    padding: var(--space-8) var(--space-6);
  }

  .resume-form {
    padding: var(--space-8) var(--space-6);
  }

  .form-row {
    gap: var(--space-5);
  }

  .form-actions {
    flex-direction: row;
    justify-content: space-between;
  }

  .btn-add,
  .btn-generate {
    width: auto;
    min-width: 140px;
  }
}

/* Large Desktop (1200px and above) */
@media (min-width: 1200px) {
  .form-content {
    max-width: 1000px;
  }

  .resume-form {
    padding: var(--space-10) var(--space-8);
  }

  .form-row {
    gap: var(--space-6);
  }
}

/* Landscape orientation adjustments */
@media (orientation: landscape) and (max-height: 600px) {
  .form-header {
    padding: var(--space-4) 0 var(--space-6) 0;
  }

  .form-title {
    font-size: 1.8rem;
  }

  .form-subtitle {
    font-size: 0.9rem;
  }
}

/* High DPI screens */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .form-input,
  .form-textarea,
  .form-select {
    border-width: 0.5px;
  }
}
